/* ------------------------------------------------------------ *\
	Button
\* ------------------------------------------------------------ */
.btn {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  appearance: none;
  background-color: #fff;
  color: #2e78f2;
  min-width: 18rem;
  line-height: 1;
  font-size: 1.5rem;
  padding: 1.5rem 2.7rem;
  border-radius: 0.7rem;
  transition: color 0.5s, background-color 0.5s;
}
.btn .btn__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn .btn__decoration {
  position: relative;
  border-radius: 50%;
  display: inline-block;
  width: 3rem;
  height: 3rem;
  background-color: #2e78f2;
  margin-right: 1rem;
  transition: background-color 0.5s;
}
.btn:hover .btn__decoration {
  background-color: #fff;
}
.btn .btn__decoration:after {
  content: "";
  position: absolute;
  border-width: 0.7rem;
  border-color: transparent transparent transparent #fff;
  border-style: solid;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-22%, -53%);
  transition: border-color 0.5s;
}
.btn:hover .btn__decoration:after {
  border-color: transparent transparent transparent #2e78f2;
}

.btn:hover {
  background-color: #2e78f2;
  color: #fff;
}

/*  Btn block  */
.btn--block {
  display: block;
  padding-left: 0;
  padding-right: 0;
}

