/* ------------------------------------------------------------ *\
	Grid
\* ------------------------------------------------------------ */
.grid {
  display: flex;
  flex-flow: row wrap;
  /*  Grid col 1of2  */
  /*  Grid col 1of3  */
  /*  Grid col 1of4  */
}
.grid .grid__col {
  max-width: 100%;
  flex: 1;
  position: relative;
}
.grid .grid__col--1of2 {
  max-width: 50%;
  flex: 0 0 50%;
}
.grid .grid__col--1of3 {
  max-width: calc(100% / 3);
  flex: 0 0 calc(100% / 3);
}
.grid .grid__col--1of4 {
  max-width: calc(100% / 4);
  flex: 0 0 calc(100% / 4);
}
@media (max-width: 975px) {
  .grid .grid__col--1of2 {
    max-width: 100%;
    flex: 0 0 100%;
  }
  .grid .grid__col--1of4 {
    max-width: calc(100% / 2);
    flex: 0 0 calc(100% / 2);
  }
}
@media (max-width: 600px) {
  .grid .grid__col--1of4 {
    max-width: 100%;
    flex: 0 0 100%;
  }
}

.grid--fullh {
  height: 100%;
}
.grid--fullh .grid__col {
  height: 100%;
  position: relative;
}

.grid__col--border {
  padding: 3.25rem;
}
.grid__col--border:after, .grid__col--border:before {
  content: "";
  position: absolute;
  bottom: 0;
  display: block;
}
.grid__col--border:before {
  left: 0;
  width: 100%;
  height: 0.4rem;
  background-color: #eaeaea;
}
.grid__col--border:after {
  left: 50%;
  width: 0%;
  height: 0.4rem;
  transform: translateX(-50%);
  background-color: #2e78f2;
  transition: width 0.5s;
}
.grid__col--border:hover:after {
  width: 100%;
}
.grid__col--border .grid__icon-hold {
  height: 9.4rem;
  width: 9.4rem;
  position: relative;
  margin-bottom: 3rem;
}
.grid__col--border .grid__icon {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transition: top 0.5s;
}
.grid__col--border:hover .grid__icon {
  top: -1.5rem;
}
.grid__col--border .grid__icon:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: 70%;
  display: block;
  background-color: rgba(27, 96, 207, 0.1);
  border-radius: 2rem;
  transition: width 0.5s, height 0.5s;
}
.grid__col--border:hover .grid__icon:before {
  width: 100%;
  height: 100%;
}
.grid__col--border .grid__body {
  color: #9b9b9b;
  font-size: 1.5rem;
  line-height: 1.667;
  margin-bottom: 2rem;
}
.grid__col--border .grid__body h4 {
  color: #000;
  margin-bottom: 1rem;
  line-height: 1.38;
  max-width: calc(100% - 0.5rem);
}
.grid__col--border .grid__btn {
  position: absolute;
  left: 3.25rem;
  bottom: 4rem;
  font-weight: 700;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none;
  appearance: none;
  background-color: #fff;
  color: #000;
  line-height: 1;
  font-size: 1.5rem;
  padding: 1rem 0;
  border-radius: 0.7rem;
  transition: color 0.5s, background-color 0.5s, padding 0.5s;
}
.grid__col--border .grid__btn .grid__btn-dec {
  position: relative;
  border-radius: 50%;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-color: #2e78f2;
  margin-right: 1rem;
  transition: background-color 0.5s;
}
.grid__col--border .grid__btn .btn__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.grid__col--border .grid__btn .grid__btn-dec:after {
  content: "";
  position: absolute;
  border-width: 0.5rem;
  border-color: transparent transparent transparent #fff;
  border-style: solid;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-22%, -53%);
  transition: border-color 0.5s;
}
.grid__col--border:hover .grid__btn {
  background-color: #2e78f2;
  color: #fff;
  padding: 1rem 1.7rem;
}
.grid__col--border:hover .grid__btn .grid__btn-dec {
  background-color: #fff;
