/* ------------------------------------------------------------ *\
	Hamburger
\* ------------------------------------------------------------ */
.hamburger {
  display: none;
  width: 4.5rem;
  height: 3rem;
  position: relative;
}
.hamburger .hamburger__line {
  position: absolute;
  width: 100%;
  height: 0.5rem;
  background-color: #000;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.5s, top 0.5s, transform 0.5s;
}
.hamburger .hamburger__line:before,
.hamburger .hamburger__line:after {
  content: "";
  position: absolute;
  height: 0.5rem;
  width: 100%;
  left: 0;
  background-color: #000;
  display: block;
}
.hamburger .hamburger__line:before {
  top: 1.35rem;
  transition: opacity 0.2s;
}
.hamburger .hamburger__line:after {
  top: 2.7rem;
  transition: transform 0.5s, top 0.5s, width 0.5s;
}
.hamburger.open {
  transform: rotate(45deg);
}
.hamburger.open .hamburger__line {
  width: 70%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.hamburger.open .hamburger__line:before {
  opacity: 0;
}
.hamburger.open .hamburger__line:after {
  top: 0;
  transform: rotate(-90deg);
}
@media (max-width: 1023px) {
  .hamburger {
    display: inline-block;
  }
}

