}
.nav li:hover {
  color: #2e78f2;
}
.nav li:after {
  content: "";
  position: absolute;
  background-color: #2e78f2;
  height: 0.4rem;
  width: 0%;
  bottom: 0;
  left: 0;
  transition: width 0.5s;
}
.nav li:hover:after {
  width: 100%;
}
.nav li + li {
  margin-left: 2.2rem;
}
.nav li a {
  display: flex;
  align-items: center;
  height: 100%;
  text-decoration: none;
}
.nav li.nav__drop {
  padding-right: 0.8rem;
}
.nav li.nav__drop:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-75%) rotate(-45deg);
  width: 0.6rem;
  height: 0.6rem;
  border-style: solid;
  border-width: 0.2rem;
  border-color: transparent transparent #000 #000;
  display: inline-block;
  transition: border-color 0.5s;
}
.nav .nav__drop:hover:before {
  border-color: transparent transparent #2e78f2 #2e78f2;
}
.nav .nav__dropdown {
  position: absolute;
  pointer-events: none;
  opacity: 0;
  padding: 2rem 4rem;
  width: 23.5rem;
  left: 0;
  top: 11rem;
  color: #9b9b9b;
  font-weight: 400;
  background-color: #fff;
  font-size: 1.4rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  transition: opacity 0.5s;
}
.nav .nav__dropdown li {
  padding: 1.2rem 0;
  position: relative;
  width: 100%;
}
.nav .nav__dropdown li:after {
  height: 0.2rem;
}
.nav .nav__dropdown li + li {
  margin-left: 0;
}
.nav .nav__dropdown li:before {
  content: "";
  background-color: #eaeaea;
  position: absolute;
  height: 0.1rem;
  bottom: 0;
  width: 100%;
}
.nav .nav__dropdown:hover .nav__dropdown {
  opacity: 1;
  pointer-events: auto;
}
.nav .nav__drop:hover .nav__dropdown {
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 1023px) {
  .nav {
    opacity: 0;
    text-align: center;
    width: 30rem;
    border-radius: 1rem;
    padding: 0 7rem;
    transition: opacity 0.5s;
    pointer-events: none;
    position: absolute;
    top: 100%;
    pointer-events: auto;
    display: block;
    top: calc(100% + 2rem);
    right: 0;
    background-color: #fff;
    height: 35rem;
  }
  .nav li {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 7rem;
    border-bottom: 0.3rem solid #9b9b9b;
  }
  .nav li:after {
    opacity: 0;
  }
  .nav li.current:after {
    bottom: -0.3rem;
    opacity: 1;
  }
  .nav li + li {
    margin-left: 0;
  }
  .nav .nav__drop {
    height: 14rem;
  }
  .nav .nav__drop > a {
    display: none;
  }
  .nav .nav__drop:before {
    opacity: 0;
  }
  .nav .nav__dropdown {
    pointer-events: auto;
    opacity: 1;
    position: relative;
    display: block;
    padding: 0;
    top: 0;
    width: 100%;
  }
  .nav .nav__dropdown li {
    color: #000;
    font-weight: 700;
  }
  .nav:before {
    content: "";
    position: absolute;
    top: -2.5rem;
    display: block;
    right: 1rem;
    border-style: solid;
    border-width: 1.5rem;
    border-color: transparent transparent #fff transparent;
  }
  .nav.open {
    pointer-events: auto;
    opacity: 1;
  }
}

